<template>
  <div>
    <div class="d-flex" v-show="!configPage" v-loading="loading">
      <template v-if="interfaceList.length > 0">
        <div
          class="itemBox"
          v-for="(item, index) in interfaceList"
          :key="index"
          @click="goPage(item)"
        >
          <div
            class="icon icon-interface"
            style="color: #24aaff; font-size: 40px"
          ></div>
          <div style="width: 55%">
            <div class="fs-18">{{ item.interface }}</div>
            <div class="fs-12 mt-10" style="color: #8e8e8e">
              <div>IP地址：{{ item.ip||'--' }}</div>
              <div class="mt-5">子网掩码：{{ item.netmask||'--' }}</div>
              <div class="mt-5">网关地址：{{ item.gateway||'--' }}</div>
            </div>
          </div>
        </div>
      </template>
      <el-empty v-else description="暂无内容" style="width: 100%"></el-empty>
    </div>
    <div v-show="configPage">
      <el-page-header @back="goBack" :content="interfaceInfo.interface">
      </el-page-header>
      <div class="d-center">
        <Wan :interfaceInfo="interfaceInfo" style="width: 40%"></Wan>
      </div>
    </div>
  </div>
</template>

<script>
import Wan from "./components/wan.vue";

export default {
  components: {
    Wan,
  },
  data() {
    return {
      interfaceList: [],
      configPage: false,
      interfaceInfo: {},
      loading: true,
    };
  },
  created() {
    this.getInterfaceList();
  },
  methods: {
    goBack() {
      this.configPage = false;
    },
    goPage(item) {
      this.configPage = true;
      this.interfaceInfo = item;
    },
    getInterfaceList() {
      this.$api
        .get("/net/getNetList")
        .then((res) => {
          this.interfaceList = res.data.data;
        })
        .finally(() => {
          this.loading = false;
        });
    },
  },
};
</script>

<style lang="scss" scoped>
.itemBox {
  border: 1px solid #a3d8f9;
  padding: 15px;
  width: 22%;
  margin-right: 20px;
  background: aliceblue;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: space-around;
}
</style>
