<template>
  <el-form
    :model="ruleForm"
    :rules="rules"
    ref="ruleForm"
    label-width="120px"
    class="demo-ruleForm"
  >
    <el-form-item label="MAC 地址:" prop="mac">
      {{ ruleForm.mac || "--" }}
    </el-form-item>
    <el-form-item label="网络类型:" prop="net_type">
      <el-select v-model="ruleForm.net_type">
        <el-option label="dhcp" :value="0"></el-option>
        <el-option label="静态地址" :value="1"></el-option>
      </el-select>
    </el-form-item>

    <template v-if="write">
      <el-form-item label="IP地址:" prop="address">
        <el-input v-model="ruleForm.address"></el-input>
      </el-form-item>
      <el-form-item label="子网掩码:" prop="netmask">
        <el-input v-model="ruleForm.netmask"></el-input>
      </el-form-item>
      <el-form-item label="网关地址:" prop="gateway">
        <el-input v-model="ruleForm.gateway"></el-input>
      </el-form-item>
    </template>
    <template v-else>
      <el-form-item label="IP地址:">
        <el-input v-model="ruleForm.address" disabled></el-input>
      </el-form-item>
      <el-form-item label="子网掩码:">
        <el-input v-model="ruleForm.netmask" disabled></el-input>
      </el-form-item>
      <el-form-item label="网关地址:">
        <el-input v-model="ruleForm.gateway" disabled></el-input>
      </el-form-item>
    </template>
    <el-form-item label="DNS服务器1:" prop="dns1">
      <el-input v-model="ruleForm.dns1"></el-input>
    </el-form-item>
    <el-form-item label="DNS服务器2:" prop="dns2">
      <el-input v-model="ruleForm.dns2"></el-input>
    </el-form-item>
    <template v-if="interfaceInfo.isWireless">
      <el-form-item label="无线网络名称:" prop="ssid">
        <el-input v-model="ruleForm.ssid"></el-input>
      </el-form-item>
      <el-form-item label="无线网络秘钥:" prop="sskey">
        <el-input
          v-model="ruleForm.sskey"
          type="password"
          show-password
        ></el-input>
      </el-form-item>
    </template>
    <el-form-item>
      <div class="d-center">
        <el-button
          type="primary"
          style="width: 80%"
          @click="submitForm('ruleForm')"
          >保存</el-button
        >
      </div>
    </el-form-item>
  </el-form>
</template>

<script>
export default {
  props: {
    interfaceInfo: {
      type: Object,
      default: () => {},
    },
  },
  data() {
    let validateIP = (rule, value, callback) => {
      const reg =
        /^(?:(?:25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.){3}(?:25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)$/;
      if (!reg.test(value)) {
        callback(new Error("格式错误 格式:XXX.XXX.XXX.XXX"));
      } else {
        callback();
      }
    };
    let validateNetmask = (rule, value, callback) => {
      const reg =
        /^(?:25[0-5]|2[0-4]\d|1\d\d|[1-9]\d|\d)(?:\.(?:25[0-5]|2[0-4]\d|1\d\d|[1-9]\d|\d)){3}$/;
      if (!reg.test(value)) {
        callback(new Error("格式错误"));
      } else {
        callback();
      }
    };
    return {
      ruleForm: {},
      rules: {
        address: [
          { required: true, message: "请输入", trigger:  ['blur', 'change'] },
          { validator: validateIP, trigger:  ['blur', 'change'] },
        ],
        netmask: [
          { required: true, message: "请输入", trigger:  ['blur', 'change'] },
          { validator: validateNetmask, trigger:  ['blur', 'change'] },
        ],
        gateway: [
          { required: true, message: "请输入", trigger:  ['blur', 'change'] },
          { validator: validateIP, trigger: ['blur', 'change'] },
        ],
        dns1: [
          { required: true, message: "请输入", trigger: ['blur', 'change'] },
          { validator: validateIP, trigger: ['blur', 'change'] },
        ],
        dns2: [
          { required: true, message: "请输入", trigger: ['blur', 'change'] },
          { validator: validateIP, trigger: ['blur', 'change'] },
        ],
      },
      interfaceList: [],
      write: true,
    };
  },
  watch: {
    "ruleForm.net_type"(newVal) {
      this.write = newVal == 0 ? false : true;
    },
    interfaceInfo() {
      this.getData();
    },
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.$api
            .post("/net/updateNet", this.ruleForm)
            .then(() => {
              this.$message.success("操作成功");
              this.getData();
            })
            .finally(() => {});
        } else {
          return false;
        }
      });
    },
    getData() {
      this.$refs.ruleForm.clearValidate();
      this.$api
        .get("/net/getNet", {
          params: {
            interface: this.interfaceInfo.interface,
          },
        })
        .then((res) => {
          let data = res.data.data;
          if (data) {
            this.ruleForm = {
              ...data,
              net_type: data.net_type == null ? 1 : data.net_type, // 设置默认值
              mac: this.interfaceInfo.mac || data.mac,
              address: this.interfaceInfo.address || data.address,
              gateway: this.interfaceInfo.gateway || data.gateway,
            };
          } else {
            this.ruleForm = {
              interface: this.interfaceInfo.interface,
              mac: this.interfaceInfo.mac,
              net_type: 1,
            };
          }
        })
        .catch(() => {
          this.ruleForm = {
            interface: this.interfaceInfo.interface,
            mac: this.interfaceInfo.mac,
            net_type: 1,
          };
        });
    },
  },
};
</script>

<style></style>
